.div-team-item {
  position: relative;
  perspective: 600px;
  -webkit-perspective: 600px;
  transition: all 0.4s ease-in-out;
}
.div-team-item .team-front {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 900;
  width: 100%;
  height: 100%;
  padding-top: 25px;
  transform: rotateX(0);
  transform-style: preserve-3d;
  backface-visibility: hidden;
  transition: all 0.4s ease-in-out;
  box-shadow: 0 0 3px 0 #d6dbee;
}
.div-team-item .team-back {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 900;
  width: 100%;
  height: 100%;
  padding-top: 25px;
  transform: rotateX(0);
  transform-style: preserve-3d;
  backface-visibility: hidden;
  transition: all 0.4s ease-in-out;
  box-shadow: 0 0 3px 0 #d6dbee;
  padding-top: 10%;
  z-index: 800;
  background: #e8e8e8;
  transform: rotateY(-179deg);
  box-shadow: none;
}
.div-team-item:hover .team-back {
  z-index: 1000;
  transform: rotateX(0);
}
.div-team-item:hover .team-front {
  transform: rotateY(179deg);
}

